* {
  font-family: 'Montserrat', sans-serif; }
.name_of_site, prev, .next {
  transition: color 0.2s ease-in-out; }
.name_of_site:hover, .prev:hover, .next:hover {
  color: #044c85; }
header .container-fluid {
  background: url("../img/background-header.png") no-repeat right;
  min-height: 954px;
  float: left;
  display: block; }
header {
  display: block;
  min-height: 954px; }
section {
  margin-top: 100px; }
.title {
  margin-top: 100px;
  h2 {
    text-transform: uppercase;
    color: #69696a; }
  p {
    color: #e7ecef; } }
.description__title {
  margin-top: 240px;
  h2 {
    color: #79796a; }
  h1 {
    color: #044c85; }
  p {
    color: #a0a096;
    line-height: 2em; } }
.next__prev {
  margin-top: 116px;
  height: 30px; }
.prev, .next {
  text-transform: uppercase;
  color: #3f3f4a;
  cursor: pointer;
  font-weight: bold;
  position: relative;
  bottom: 9px; }
.prev:before {
  content: '←';
  margin-right: 45px; }
.next:after {
  content: '→';
  margin-left: 45px; }
.punts {
  margin: 0 18px;
  height: 30px;
  display: inline-block; }
.punt {
  height: 30px;
  width: 30px;
  cursor: pointer;
  border: 5px solid #999999;
  border-radius: 50%;
  display: inline-block; }
.active {
  padding: 0 10px;
  border: 5px solid #5134db;
  .active__element {
    background: #5134db;
    width: 14px;
    height: 14px;
    border-radius: 50%;
    top: 3px;
    display: block;
    position: relative;
    right: 7px; } }
@media screen and (max-width: 1419px) and (min-width: 1201px) {
  .button {
    width: 45%;
    margin: 0 30px 0 30px; }
  .description {
    p {
      width: 60%; } } }
@media screen and (max-width: 800px) {
  .button {
    width: 100%;
    margin: 0 0 0 0; }
  .img {
    width: 50%; }
  .description {
    p {
      width: 100%; } }
  .newsletter form {
    button {
      border-top-left-radius: 100px;
      border-bottom-left-radius: 100px; } } }
@media screen and (max-width: 1200px) and (min-width: 801px) {
  .button {
    width: 40%;
    margin: 0 30px 0 30px; }
  .img {
    width: 60%; }
  .description {
    p {
      width: 70%; } } }
@media screen and (min-width: 1420px) {
  .button {
    width: 20%;
    margin: 0 30px 0 30px; }
  .description {
    p {
      width: 50%; } } }
.button {
  overflow: hidden;
  display: inline-block;
  position: relative;
  height: 95px;
  box-shadow: 0 0 5px 1px rgba(191, 191, 255, 0.85);
  background-image: linear-gradient(-248.92deg, #6048ff 0%, #7920e6 100%); }
#google_play {
  padding-top: 28px;
  padding-left: 15px;
  padding-bottom: 28px; }
.google_text {
  display: inline-block;
  color: #fefeff;
  font-size: 25px;
  float: left; }
.button-url {
  background: #f4f6f7;
  padding: 7px 0 0 25px;
  height: 95px;
  text-align: left; }
.google {
  border: 20px solid transparent;
  border-left: 30px solid white;
  display: inline-block;
  float: left; }
.apple_icon {
  height: 95px;
  float: left; }
.apple_text {
  display: inline-block;
  margin-left: 10%;
  padding-top: 15px; }
.apple_text span {
  color: #4f5050; }
.apple_text h2 {
  color: #2f2f2f; }
.amazon__app {
  display: block;
  margin: 30px auto; }
.steam__app {
  display: block;
  margin: 8px auto; }
.description {
  margin-top: 100px;
  text-align: center;
  img {
    text-align: center;
    display: inline-block; }
  p {
    color: grey;
    display: inline-block; }
  .buzy_button {
    text-transform: uppercase;
    display: block;
    margin: 50px auto 0 auto;
    background: #6048ff;
    border: none;
    border-radius: 100px;
    color: white;
    width: 366px;
    height: 80px;
    box-shadow: 0 0 5px 1px rgba(191, 191, 255, 0.85);
    font-weight: bold; } }
.newsletter h1 {
  color: #3c4b74; }
.newsletter p {
  color: #9ca1ab; }
.newsletter form {
  margin-top: 100px;
  button {
    border: none;
    background: linear-gradient(#6048fe, #7739dc);
    padding: 35px;
    border-top-right-radius: 100px;
    border-bottom-right-radius: 100px;
    color: white;
    font-size: 18px;
    font-weight: bold;
    box-shadow: 0 0 5px 1px rgba(191, 191, 255, 0.85);
    position: relative;
    left: -3px; }
  input {
    border: none;
    padding: 35px;
    color: #818894;
    font-size: 18px;
    font-weight: bold;
    box-shadow: 0 0 5px 1px rgba(191, 191, 255, 0.85); } }
.links {
  margin-top: 135px;
  margin-bottom: 40px; }
/* Footer */
footer {
  background: #fafbff;
  height: 130px;
  width: 100%;
  padding: 55px;
  text-align: center;
  a {
    text-decoration: none; }
  a:hover {
    text-decoration: none; }
  span {
    color: #aaaaaa;
    font-weight: bold;
    text-decoration: none;
    text-transform: uppercase;
    transition: color 0.2s ease-in-out; }
  span:hover {
    color: #000; }
  .first span {
    font-size: 16px;
    margin-right: 65px; }
  .second span {
    font-size: 14px;
    margin-right: 32px; }
  span.active_footer {
      color: #000000; } }
